@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #222;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  box-sizing: border-box;
}

.App {
  background: rgb(14, 168, 186);
  background: linear-gradient(45deg, rgba(14, 168, 186, 1) 0%, rgba(140, 18, 235, 1) 100%);
  border-radius: 10px;
  color: white;
  width: 60vw;
  margin: 20px auto;
  padding: 20px;
  font-family: 'Open Sans', sans-serif;
}

h1,
h2 {
  text-align: center;
}

.nothing {
  display: block;
  text-align: center;
  margin: 30px;
  font-size: 20px;
}

@media (max-width: 1024px) {
  .App {
    width: 80vw;
  }
}

@media (max-width: 600px) {
  .App {
    width: 100vw;
    border-radius: 0;
    margin: 0 auto;
    padding: 15px;
  }

}
.form input {
  padding: 8px;
  margin-bottom: 15px;
  background-color: rgba(255, 255, 255, 0.137);
  color: white;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.form label {
  display: block;
}

.form .priority-checkbox {
  display: inline-block;
  margin-left: 10px;
}

.form input[type="text"] {
  width: 60%;
}

.form input[type="date"] {
  width: 180px;
}

.form button {
  display: block;
  width: 80%;
  margin: 20px auto;
  padding: 10px;
  background-color: rgba(245, 245, 245, 0.89);
  border: 1px solid white;
  border-radius: 50px;
  cursor: pointer;
  font-size: 16px;
  transition: 0.2s;
}

.form button:hover {
  background-color: transparent;
  color: whitesmoke;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgba(255, 255, 255, 0.336);
  opacity: 1;
  /* Firefox */
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgba(255, 255, 255, 0.336);
  opacity: 1;
  /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgba(255, 255, 255, 0.336);
  opacity: 1;
  /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: white;
}

::-ms-input-placeholder {
  /* Microsoft Edge */
  color: white;
}

input:focus {
  outline-color: white;
  outline-width: 1px;
}

::-webkit-calendar-picker-indicator {
  -webkit-filter: invert(1);
          filter: invert(1);
  cursor: pointer;
}

@media (max-width: 600px) {
  .form input[type="text"] {
    width: 100%;
  }

  .form .priority-checkbox {
    text-align: right;
    display: block;
  }
}

.task-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgba(255, 255, 255, 0.329);
    color: white;
    margin: 10px;
    padding: 12px;
    border-radius: 20px;
    min-height: 60px;
    font-size: 16px;
}

.task-wrapper:hover {
    box-shadow: 0px 0px 13px 3px rgba(255, 255, 255, 0.836);
}

.task-wrapper strong {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    margin: 0 5px;
}

.task-wrapper .date {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 13px;
    font-weight: 200;
    color: rgba(255, 255, 255, 0.678);
}

.task-wrapper button {
    background-color: transparent;
    border: none;

}

.task-wrapper i {
    font-size: 20px;
    padding: 5px;
    cursor: pointer;
    transition: .2s;
}

.task-wrapper i:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
}

.fa-check-circle {
    color: rgba(6, 255, 6, 0.651);
}

.fa-trash-alt {
    color: rgb(44, 43, 43);
}

.fa-times-circle {
    color: rgba(255, 0, 0, 0.651);
}


@media (max-width: 600px) {
    .task-wrapper {
        display: -webkit-flex;
        display: flex;
        -webkit-flex-wrap: wrap;
                flex-wrap: wrap;
        -webkit-justify-content: center;
                justify-content: center;
    }

    .task-wrapper>* {
        -webkit-flex-basis: 100%;
                flex-basis: 100%;
        -webkit-align-content: center;
                align-content: center;
        text-align: center;
    }

    .task-wrapper .date {
        margin-top: 5px;
    }

    .task-wrapper button {
        -webkit-flex-basis: 10%;
                flex-basis: 10%;
        margin: 0 30px;
    }
}
